<template>
    <div class="own_b">
        <div class="own" @click="closemune()"></div>
        <div class="own_2">
          <div class="code_list">
            <yd-flexbox v-for="item in codelist" class="code_item" :class="{'bj':item.check}" @click.native="addclass(item)">
                <div>
                  <!-- <img :src="item.imgurl"> -->
                  <canvas  :id="item.src" :style="{width: '.8rem', height: '.8rem'}" style="border-radius:50%;margin: 0 .2rem 0 .4rem"></canvas>
                </div>
                <yd-flexbox-item>{{item.name}}</yd-flexbox-item>
            </yd-flexbox>
          </div>
          <div class="code_nav">
            <yd-flexbox class="code_sao" @click.native="canCode()">
                <div><img src="../assets/images/code_sao.png"></div>
                <yd-flexbox-item>{{$t('header.sao')}}</yd-flexbox-item>
            </yd-flexbox>
            <yd-flexbox class="code_sao" @click.native="gocreatecode()">
                <div><img src="../assets/images/code_create.png"></div>
                <yd-flexbox-item>{{$t('header.createcode')}}</yd-flexbox-item>
            </yd-flexbox>
          </div>
        </div>
    </div>
  
</template>

<script>
   import {getCookie,setCookie} from '../assets/js/cookie.js'
   import { ToZhuanZhang,Getvuefghfvmdfgdfgdfg,Setvuefghfvmdfgdfgdfg } from '../assets/js/Keytoken.js'
export default {
  data () {
    return {
      codelist:'',
      src:'',
      result:'',
      id:'drawing'
    }
  },
  created(){
    var codelist = getCookie("codelist")
    if(codelist&&codelist!=undefined&&codelist!=null){
      var list = JSON.parse(codelist)
      if (list) {
        for (var i = 0; i < list.length; i++) {
          if (getCookie("publickey")  == list[i].publickey) {
            list[i].check = true
          }else{
            list[i].check = false
          }
          list[i].src = 'drawing'+i
        }
        this.codelist = list
      }
    }
    window.ToZhuanZhang=ToZhuanZhang;
  },
  mounted () {
      var list = this.codelist
      for (var i = 0; i < list.length; i++) {
        this.DrawRandomPic((list[i].publickey).substring(2,42),list[i].src)
      }
  },
  methods:{
    closemune(){
      this.$emit('mune')
    },
    canCode(){
      Setvuefghfvmdfgdfgdfg(this);
      window.QRUtil.startScanQR('1','ToZhuanZhang')
    },
    //跳转创建钱包
    gocreatecode(){
      this.$router.push({path:'/createcode'})
    },
    addclass(item){
      var list = this.codelist
     this.openloading(this.$t('data.Inswitch'))
      var that = this
      setTimeout(function () {
          for (var i = 0; i < list.length; i++) {
            if (item.publickey == list[i].publickey) {
              list[i].check = true
              setCookie("publickey",item.publickey)
              setCookie("Currency_list","")
              var info = JSON.stringify(item)
              setCookie("info",info,true)
            }else{
              list[i].check = false
            }
          }
          that.closeloading()
          var jsonStr = JSON.stringify(list)
          setCookie("codelist",jsonStr)
          that.$parent.$parent.loadList()
          that.$parent.$parent.walletinfo()
          that.closemune()
      },10)
      
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .own{
      position: fixed;
      width: 100%;
      height: 100%;
      z-index: 1099;
      background: rgba(0,0,0,.4);
      top: 0;
      left: 0;
      font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
    }
    .own_2{
      width: 4.5rem;
      position: fixed;
      height: 100%;
      right: 0;
      top: 0;
      background: #fff;
      z-index: 1099;
    }
    .code_item{
      font-size: .25rem;
      color: #212e3b;
      padding: .22rem 0;
    }
    .code_item:active{
      background: #ffeaea
    }
    .code_item img{
      width: .8rem;
      height: .8rem;
      border-radius: 50%;
      border:1px solid #e7f1fa;
      margin: 0 .2rem 0 .4rem;
    }
    .bj{
      background: #ffeaea!important
    }
    .code_list{
      margin-top: .9rem;
      padding: 0 0 .6rem 0;
      border-bottom: 1px solid #f4f4f4;
      overflow-y: auto;
      max-height: 5rem;
    }
    .code_nav{
      padding-top: .2rem;
    }
    .code_sao{
      font-size: .25rem;
      margin-top: .4rem;
    }
    .code_sao img{
      width: .44rem;
      height: .44rem;
      margin-left: .58rem;
      margin-right: .2rem
    }
  .code_list::-webkit-scrollbar {/*滚动条整体样式*/
        width: 0px;     /*高宽分别对应横竖滚动条的尺寸*/
        height: 1px;
    }
  .code_list::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
          border-radius: 10px;
          -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
          background: #535353;
      }
  .code_list::-webkit-scrollbar-track {/*滚动条里面轨道*/
          -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
          border-radius: 10px;
          background: #EDEDED;
    }
</style>



