<template>
    <div class="index" v-show="indexshow">
        <yd-pullrefresh :callback="Currencyload" ref="pullrefreshDemo" loading-text="" pull-text="" drop-text="">
            <!-- ref="pullrefreshDemo" -->
        <yd-flexbox direction="vertical" class="content_top">
            <yd-flexbox-item class="content_mune">
                <yd-flexbox>
                    <!-- 二维码  -->
                    <yd-flexbox-item>
                        <div class="addre">
                            <img @click="goqcode(info)" id="qrcode" src="../assets/images/59.png">
                            <span @click="goqcode(info)">{{info.publickey1}}</span>
                        </div>
                    </yd-flexbox-item>
                    <!-- 右侧菜单 -->
                    <img @click="mune()" src="../assets/images/mune.png">
                </yd-flexbox>
            </yd-flexbox-item>
            <yd-flexbox-item class="content_mycenter">
                <yd-flexbox style="padding-bottom: .3rem">
                    <div class="mypic">
                        <canvas @click="requirecode(info)" id="drawing" :style="{width: '1.4rem', height: '1.4rem'}" style="border-radius: 50%;box-shadow:2px 2px 10px #555;border:1px solid #ddd"></canvas>
                        <p @click="requirecode(info)">{{info.name | namefilter(4)}}</p>
                    </div>
                    <yd-flexbox-item>
                        <div class="price_left nwtotal">
                            <yd-countup  
                                :class="{'ft20':taotalnum.length>8}" 
                                :endnum="taotalnum" 
                                duration="1" 
                                decimals="4" 
                                prefix=""
                                > 
                            </yd-countup>
                            <span>{{$t('index.total')}}（{{coin}} )</span>
                        </div>
                    </yd-flexbox-item>
                </yd-flexbox>
            </yd-flexbox-item>
        </yd-flexbox>
        <div class="pro_list">
            <yd-flexbox class="pro_list_item" v-for="item in list" @click.native="gocodedetail(item)">
                <yd-flexbox-item  class="pro_list_img">
                    <div class="imgwai"><img :src="item.currIconPath"></div>
                    <span>{{item.currName}}</span>
                </yd-flexbox-item>
                <yd-flexbox-item  class="pro_list_price">
                    <span>{{item.Balance}}</span>
                    <span>~ {{coin}} {{item.Amount}}</span>
                </yd-flexbox-item>
            </yd-flexbox>
        </div>
        <div></div>
        <!-- 个人中心 -->
        <own v-show="ownshow" @mune="mune()"></own>
        <!-- 尾部 -->
        <footerBar :footerdata="footerdata"></footerBar>
    </yd-pullrefresh>
    <!-- 选择语言 -->
    <yd-popup v-model="language" position="center" width="70%" :close-on-masker="false">
        <div style="background-color:#fff;border-radius: 8px;">
            <ul class="languagelist">
                <li :class="{'liactive':languagetype=='zh'}" @click="chooseType('zh','+86')">中文</li>
                <li :class="{'liactive':languagetype=='en'}" @click="chooseType('en','+44')">English</li>
                <li :class="{'liactive':languagetype=='jp'}" @click="chooseType('jp','+81')">日本語</li>
                <li :class="{'liactive':languagetype=='han'}" @click="chooseType('han','+82')">한글</li>
            </ul>
            <!-- <p style="text-align: center;">
                <button @click="ChooseLanguage()">choose language</button>
            </p> -->
        </div>
    </yd-popup>
    <div class="add" 
        @touchmove="touchmove"
        @touchstart="touchstart"
        :style="{top:top, left: left}"
        >
        <img src="../assets/images/add.png" @click="goindex_set()">
    </div>
    </div>
</template>
<script>
    import QRCode from 'qrcodejs2'
    import headerBar from './header.vue'
    import footerBar from './footer.vue'
    import own from './own.vue'
    import { getTokenQty } from '../assets/js/Keytoken.js'
    import { bindAndriodToken } from '@/api/android'
    import {getCookie,setCookie,getCookieWithDefault} from '../assets/js/cookie.js'
    export default {
        components:{
            "headerBar":headerBar,
            "footerBar":footerBar,
            "own":own
        },
        data () {
            return {
              footerdata:{
                bj:'#fdfdff'
              },
              loding:this.$t('toast.loding'),
              indexshow:false,
              ownshow:false,
              list:[],
              coin:getCookieWithDefault('coin','CNY'),
              taotalnum:0,
              info:'',
              src:'',
              pubkey:getCookie("publickey"),
              isok:false,
              sdfsd:'',
              arry:[],
              top: '77%',
              left: 'calc(100% - 50px)',
              list_list:[],
              phoneToken:'',
              language:false,
              languagetype:'zh'
            }
        },
        created(){
            if(!getCookie("lang") || getCookie("lang")==undefined || getCookie("lang")==null){
                this.language = true
            }
            this.walletinfo()
            this.indexshow=true
            // 创建实例前加载数据
            this.loadList(1) 
            // 软件提示更新
            this.isdwonload()
        },
        mounted () {
            var codelist = getCookie("codelist")
            if (codelist&&codelist!=""&&codelist!="null"&&codelist!="undefined"){
                this.DrawRandomPic(getCookie("publickey").substring(2,42),"drawing")
            }
        },
        methods:{
            // 选择语言
            chooseType(type,sym){
                this.languagetype = type
                setCookie("lang",type)
                setCookie("fromphone",sym)
                this.language = true
                this.$router.go(0)
            },
            ChooseLanguage(){},
            //显示隐藏菜单
            mune(){
                this.ownshow = !this.ownshow
                //this.loadList()
                //this.walletinfo()
            },
            //生成二维码
            qrcode () {  
                let qrcode = new QRCode('qrcode', {  
                    width: 15,  
                    height: 15, 
                    text: '565sdf55652dfc55gh23' 
                })   
            }, 
            walletinfo(){
                //初始钱包列表
                var codelist = getCookie("codelist")
                var publickey = getCookie("publickey")
                if (codelist&&codelist!="undefined"&&codelist!="null") {
                    var data = JSON.parse(getCookie("codelist"))
                    if (publickey&&publickey!="undefined"&&publickey!="null") {
                        for (var i = 0; i < data.length; i++) {
                            if(data[i].phoneToken==undefined||data[i].phoneToken==null||data[i].phoneToken==""){
                                //查看钱包是否绑定手机id
                                if(window.phoneToken){
                                    var NewPhoneToken = window.phoneToken.tokenId()
                                    bindAndriodToken(data[i].publickey,NewPhoneToken)
                                }
                            }
                            if (getCookie("publickey") == data[i].publickey) {
                                var pu = data[i].publickey.substring(0,8)
                                var pu1 = data[i].publickey.substring(34,data[i].publickey.length)
                                data[i].publickey1 = pu + "..." + pu1
                                this.info = data[i]
                            }
                        }
                    }else{
                        var data = JSON.parse(getCookie("codelist"))[0]
                        var pu = data.publickey.substring(0,8)
                        var pu1 = data.publickey.substring(34,data.publickey.length)
                        data.publickey1 = pu + "..." + pu1
                        this.info = data
                        setCookie("publickey",data.publickey)
                    }
                }else{
                    this.$router.replace({path:"/createcode",query:{del:1}})
                }
            },
            touchmove(e) {
                var x = e.touches[0].clientX
                var y = e.touches[0].clientY
                var w = document.documentElement.clientWidth //可见区域宽度
                var h = document.documentElement.clientHeight // 可见区域高度
                if (x > w-100 ) {
                    this.left='calc(100% - 50px)'
                }else if (x < 0) {
                    this.left='10px'
                } else{
                    this.left = x + 'px'
                }
                if (y > h-100) {
                    this.top =h-100 + 'px'
                }else if (y < 0) {
                    this.top ='10px'
                } else{
                    this.top = y + 'px'
                }
            },
            touchstart(e) {
                this.left = e.touches[0].clientX-12 + 'px'
                this.top = e.touches[0].clientY-12 + 'px'

            },
            //跳转资产详情
            gocodedetail(item){
                this.$router.push({path: "/index_detail",query:{ name:item.currName,price:item.currPrice,contractAddress:item.contractAddress}});
            },
            //跳转二维码详情
            goqcode(item){
                this.$router.push({path: "/qcode",query:{ publickey:item.publickey,src:item.src}});
            },
            //修改钱包信息
            requirecode(item){
                this.$router.push({path: "/requirecode",query:{ publickey:item.publickey,name:item.name}});
            },
            //闪兑
            goshandui(){
                this.$router.push({path: "/shandui",query:{ num:'565sdf55652dfc55gh23',name:'星巴克、kunt'}});
            },
            //跳转资产管理添加
            goindex_set(){
                this.$router.push({path: "/index_set",query:{ num:'565sdf55652dfc55gh23'}});
            },
            loadList(num) {
                var that = this;
                this.taotalnum=0
                this.Currencylist(this,num)
            },
            Currencyload(){
                this.loadList(2)
            },
            //加载数据列表
            Currencylist(that,num){
                that.list=[]
                if (num == 1) {
                    that.openloading('加载中...')
                }
                this.axios({
                  url:that.Url + 'ShowCurrency.ashx',
                  method:'post',
                  headers:{'Content-Type':'application/x-www-form-urlencoded'},
                  params: {req:4,publickey:getCookie('publickey'),moneycode: getCookieWithDefault('coin','CNY')}
                }).then(function (res) {
                    that.closeloading()
                    that.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
                    if(res.data.success){
                        var arry = res.data.data
                        if (arry.length == 0) {
                            that.loadtext = '没有更多数据'
                            return;
                        }else{
                            arry.forEach(item => {
                                item.coinprice = (item.Balance*item.currPrice).toFixed(4)
                                item.currIconPath = that.imgUrl+item.currIconPath
                            });
                            that.list = arry
                            that.total(that.list)
                        }
                    }else{
                        
                    }
                }).catch(function(res){
                    that.closeloading()
                    res = JSON.stringify(res)
                    that.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
                    if(JSON.stringify(JSON.parse(res).request)=="{}"){
                        that.catchErr()
                        setTimeout(function(){
                            that.$dialog.confirm({
                                title: '网络不给力请重新加载...',
                                opts: (res) => {
                                    that.loadList(2) 
                                }
                            });
                        },2000)
                    }
                });
            },
            //计算总资产
            total(item){
                var that = this
                that.taotalnum=0
                if(item){
                    for (var i = 0; i < item.length; i++) {
                        that.taotalnum += parseFloat(item[i].Amount)
                        
                    }
                }
            },
            //查询软件更新
            isdwonload(){
                var that = this
                this.axios({
                  url:this.Url + 'Upgrade.ashx?a=1',
                  method:'post',
                  headers:{'Content-Type':'application/x-www-form-urlencoded'},
                }).then(function (res) {
                    if (res.data.success) {
                        that.dwonloadmsg = res.data.data.downLoadUrl
                        var version = res.data.data.version
                        if(version != that.appVersion){
                            that.openConfrim() 
                        }
                    }
                })
            },
            //软件更新提示框
            openConfrim() {
                this.$dialog.confirm({
                    title: '更新提示',
                    mes: "检测到新版本，请先备份【钱包私钥】及相关信息，然后点击【确定】。",
                    opts: (res) => {
                        window.FileDown.FileDownLoad(this.dwonloadmsg)
                        //window.location.href = this.dwonloadmsg
                    }
                });
            },
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/css/index.css'
</style>
<style>
.yd-pullrefresh-dragtip {
    pointer-events: none;
    position: absolute;
    top: 50px!important;
    left: 50%!important;
    -webkit-transform: translate(0);
    transform: translate(0);
    width: 36px!important;
    height: 36px;
    margin-left:-18px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: #6e6e6e;
    font-size: 14px;
    text-align: center;
    background-color: rgba(240,250,250,.9);
    z-index: 9999!important;
    border-radius: 50%;
}
.yd-pullrefresh-dragtip-icon>i{
  margin:0!important
}
</style>
<style>
.languagelist{
    padding:0 .3rem;
}
.languagelist li{
    height: 45px;
    line-height:45px;
    font-size:.3rem;
    color:#2a2a2a;
    text-align:left;
    border-bottom:1px solid #f1f1f1;
    background:url('../assets/images/wc.png') no-repeat right center;
    background-size:25px 25px;
}
.languagelist li:last-child{
    border-bottom:none;
}
.liactive{
    background:url('../assets/images/xz.png') no-repeat right center!important;
    background-size:25px 25px!important;
    color:#3295ef!important
}
</style>




